/* Theme */
.dark{
  background-color: #333;
  color: white;
  height: 100%;
  }
body{
  margin: 0;
  padding:0;
  background-color: white;
  color: #333;
  height:100%;
  height: 100%;
  transition: background-color 0.25s ease-in-out;
}

/* Dark & Light theme toggle button */
 .toggleContainer{
  display: flex;
  justify-content: flex-end;
  padding: 30px;
}
/* .toggleContainer{
  display: flex;
  justify-content: flex-end;
  padding: 30px;
}*/
.switchContainer{
  width:80px;
  height:31px;
  padding-left: 15%;
  border-radius: 16px;
  background-color: #D2D2CF;
  position: relative;
  letter-spacing: 2px;}

  .switchContainer2{
    width:100px;
    height:31px;
    border-radius: 16px;
    background-color: #D2D2CF;
    position: relative;
    letter-spacing: 2px;
  margin-left: 5%;}

.switchContainer:hover{
  transform: translateY(-7px);
  transition: 0.3s;
}
.switchContainer2:hover{
  transform: translateY(-7px);
  transition: 0.3s;
}
